<template>
    <div class="tutorial-box">
        <h1>Hand Shadow Tutorial</h1>
        <div class="img-box" :class="{flip: currentPos}">
            <img :src="imgDir + step + '.jpg'">
            <DetectCam 
                v-bind:step="step"
                v-bind:finish="finish"
                v-on:nextstep="gotoNextFrame"/>
            </div>
        <div @click="gotoNextFrame" class="instruction">Step {{step}}: {{instructions[step]}}</div>
    </div>
</template>

<script>
import DetectCam from './DetectCam.vue'

export default {
  name: 'TutorialBox',
  props: ['currentPos'],
  data () {
      return {
            step: 1,
            finish: false,
            imgDir: 'images/',
            instructions: [
                '',
                'Make your right hand in this shape.',
                'Use your left hand to adjust the right hand finger',
                'Adjust your right hand middle finger to make a round face.',
                'Put your left hand over right hand!',
                'You dit it!',
            ],
      }
  },
  methods: {
        gotoNextFrame () {
            if (this.step === this.instructions.length - 1) {
                this.finish = true;
                return false;
            }
            this.step++
        }
  },
    components: {
        DetectCam
  }
}
</script>

