<template>
    <div class="cam-box">
        <img width="100%" :src="screenshot">
        <div class="match">Step{{step}}: {{matchPercentage}}% match.</div>
        <a @click="pauseCam" href="#" class="pause">{{pause ? 'Resume' : 'Pause'}}</a>
    </div>
</template>

<script>
import axios from 'axios';
import $ from 'jquery';
import config from '../../config.js'

const visionApi = config.visionApi
const predictionKey = config.predictionKey
const imageServer = config.imageServer

export default {
    name: 'DetectCam',
    props: ['step', 'finish'],
    data () {
        return {
            pause: true,
            threshold: 0.4,
            title: 'Follow the steps', 
            screenshot: '',
            matchPercentage: 0,
            checking: false
        }
    },
    mounted () {
        this.initWebCam();
    },
    methods: {
        pauseCam() {
            this.pause = !this.pause
        },
        camReady (){
            setInterval(() => {
                !this.checking 
                && !this.pause 
                && !this.finish 
                && this.captureImage();
            }, 1000);
        },
        formatResult (res) {
            var predictionsObj = {}
            res.data.predictions.forEach(v => {
                predictionsObj[v.tagName] = v.probability
            })
            var key = 's' + this.step
            this.matchPercentage = parseInt(predictionsObj[key] * 10000) / 100
            if (predictionsObj[key] > this.threshold) {
                console.log('Match percentage: ', this.matchPercentage)
                setTimeout(() => {
                    this.matchPercentage = 0
                    this.$emit('nextstep')
                    this.checking = false;
                },1000)
                return false;
            }
            this.checking = false;
        },
        initWebCam () {
            var video = document.createElement('video');
            video.id = "screenshot-video"
            video.style.width = '100%';
            video.style.opacity = '0.5';
            video.style.display = 'none';
            video.setAttribute('autoplay', '');
            video.setAttribute('muted', '');
            video.setAttribute('playsinline', '');
            // Good to go!
            var facingMode = ['user', 'environment'];
            var constraints = {
                audio: false,
                video: {
                    facingMode: facingMode[1]
                }
            }
            navigator.mediaDevices.getUserMedia(constraints)
                .then(stream => {
                    video.srcObject = stream;
                    this.camReady();
                });
            document.getElementsByClassName('cam-box')[0].appendChild(video);
        },
        captureImage () {
            const video = document.querySelector('#screenshot-video');
            if (video.videoWidth === 0) { return false; }
            const canvas = document.createElement('canvas');
            const ctx = canvas.getContext('2d')
            canvas.width = video.videoWidth /2;
            canvas.height = video.videoHeight /2;
            ctx.drawImage(video, 0, 0, canvas.width, canvas.height);
            this.screenshot = canvas.toDataURL('image/png');
    
            this.uploadImage().then(res=> {
                return this.checkVision(imageServer + 'test.png')
            }).then(res => {
                return this.formatResult(res);
            })
        },
        uploadImage (imageData) {
            this.checking = true;
            return axios({
                method: 'post',
                url: imageServer + 'upload/',
                data: {
                    url: this.screenshot
                }
            })
        },
        checkVision (url) {
            return axios({
                method: 'post',
                url: visionApi,
                headers: {
                    'Prediction-Key': predictionKey,
                    'Content-Type': 'application/json'
                },
                data: {
                    'Url': url
                }
            })
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.cam-box {
    position: absolute;
    width: 100%;
    top: 0;
    left: 0
}
img {
    opacity: 0.5;
    border-radius: 3px;
}
.match {
    font-size: 20px;
    color: white;
    position: absolute;
    left: 0;
    top: 0;
}
.pause {
    position: absolute;
    right: 0;
    top: 0;
}
</style>
