<template>
  <div id="app">
      <div class="main-box" :class="{'reverse': currentPos}">
            <ScreenBox
                v-bind:current-pos="currentPos"
                v-on:switchpos="switchPos"
             />
            <TutorialBox 
                v-bind:current-pos="currentPos"
            />
      </div>
  </div>
</template>

<script>
import TutorialBox from './components/TutorialBox.vue'
import ScreenBox from './components/ScreenBox.vue'

export default {
  name: 'app',
  data () {
        return {
            currentPos: 0,
        }
  },
  methods: {
        switchPos() {
            this.currentPos = Number(!this.currentPos)
        }
  },
  components: {
        ScreenBox,
        TutorialBox
  }
}
</script>

